/* #212e37 */
.Navigation{
    background:#fff ;
    -webkit-box-shadow: 0 0.5px 5px #666;/* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow:    0 0.5px 5px #666;  /* Firefox 3.5 - 3.6 */
    box-shadow:         0 0.5px 5px #666;
    padding:0.5rem 0px ;
}
.Navigation.dark-element-bg{
    -webkit-box-shadow: 0 0.5px 5px #111517;/* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow:    0 0.5px 5px #111517;  /* Firefox 3.5 - 3.6 */
    box-shadow:         0 0.5px 5px #111517;
    padding:0.5rem 0px ;
}
.dark-element-bg{
    background: hsl(209, 23%, 22%);
    color:hsl(0, 0%, 100%); 
}




.nav-bar{    
    display:flex;
    justify-content: space-between;
    padding: 0.75rem;
}


