.btn{
    background: white;
    padding: 2px;
    height: 27px;
    display:flex;
    margin-right: 5px;
    margin-bottom: 5px;
    width: 7rem;
    overflow: hidden;
    font-size: 14px;
    border-radius: 3px;
    justify-content: center;
    align-items:center;
    -webkit-box-shadow: 0px 0px 5px 0px #bbbaba;/* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow:    0px 0px 5px 0px #bbbaba;  /* Firefox 3.5 - 3.6 */
    box-shadow:         0px 0px 5px 0px #bbbaba;
}
.btn.element-bg{
    background: hsl(209, 23%, 22%);
    color: white;
    padding: 2px;
    height: 27px;
    margin-right: 5px;
    margin-bottom: 5px;
    display:flex;
    width: 7rem;
    overflow: hidden;
    font-size: 14px;
    border-radius: 3px;
    justify-content: center;
    align-items:center;
    -webkit-box-shadow: 0px 0.3px 2px 0px #111517;/* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow:    0px 0.3px 2px 0px #111517;  /* Firefox 3.5 - 3.6 */
    box-shadow:         0px 0.3px 2px 0px #111517;
}

.list{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: flex-start;
}