.btnBack{
background: white;
padding: 7px;
width: 120px;
display:flex;
border-radius: 5px;
align-items:center;
justify-content: center;
-webkit-box-shadow: 0px 0px 5px 0px #bbbaba;/* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
-moz-box-shadow:    0px 0px 5px 0px #bbbaba;  /* Firefox 3.5 - 3.6 */
box-shadow:         0px 0px 5px 0px #bbbaba;
}
.btnBack.element-bg{
background:  hsl(209, 23%, 22%);
color: white;
padding: 7px;
width: 120px;
display:flex;
border-radius: 5px;
align-items:center;
justify-content: center;
-webkit-box-shadow: 0px 0.3px 2px 0px #111517;/* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
-moz-box-shadow:    0px 0.3px 2px 0px #111517;  /* Firefox 3.5 - 3.6 */
box-shadow:         0px 0.3px 2px 0px #111517;
}
