

.input-body{
    background: white;
     display:flex;
    padding:0.5rem;
    width:400px;
    height: 45px;
    align-items:center;
    -webkit-box-shadow: 0px 0.3px 2px 0px #bbbaba;/* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow:    0px 0.3px 2px 0px #bbbaba;  /* Firefox 3.5 - 3.6 */
    box-shadow:         0px 0.3px 2px 0px #bbbaba;
}
.input-body.element-bg{
    background: hsl(209, 23%, 22%);
    color: white;
    -webkit-box-shadow: 0px 0.3px 2px 0px #111517;/* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow:    0px 0.3px 2px 0px #111517;  /* Firefox 3.5 - 3.6 */
    box-shadow:         0px 0.3px 2px 0px #111517;
}


.search-input{width: 90%;
padding: 0.5rem;
resize: none;
user-select: none;
color: white;
border: none;
font-size: 1rem;
border-radius: 4px;
outline: none;
background: transparent;
}