*
{
    user-select: none;
}

*:focus
{
    outline: none;
}


.brd
{
    border: 1px solid #e2eded;
    border-color: #eaf1f1 #e4eded #dbe7e7 #e4eded;
    padding: 12px 14px;
    background-color: #fff;
    border-radius: 4px;
    cursor: pointer;
}
.brd.element-bg
{
    border: 1px solid hsl(207, 26%, 17%);
    border-color: hsl(207, 26%, 17%) hsl(207, 26%, 17%) hsl(207, 26%, 17%) hsl(207, 26%, 17%);
    padding: 12px 14px;
    border-radius: 4px;
    cursor: pointer;
    color:hsl(0, 0%, 100%); 
    background: hsl(209, 23%, 22%);
}

/* .dark-element-bg{
    background: hsl(209, 23%, 22%);
    color:hsl(0, 0%, 100%); 
} */



#app-cover
{
    position: relative;
    top: 0;
    right: 0;
    left: 0;
    width: 200px;
    height: 42px;
    /* margin: 100px auto 0 auto; */
}

#select-button
{
    position: relative;
    padding: 12px 14px;
    border-radius: 4px;
    cursor: pointer;
}

#options-view-button
{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    opacity: 0;
    cursor: pointer;
    z-index: 3;
}

#selected-value
{
    font-size: 16px;
    line-height: 1;
    margin-right: 26px;
}

.option i
{
    width: 16px;
    height: 16px;
}

.option, .label
{
    font-size: 16px;
}
.option.element-bg{
    background-color: hsl(209, 23%, 22%); 
    color: white;
}

#chevrons
{
    position: absolute;
    top: 0;
    right: 12px;
    bottom: 0;
    width: 12px;
    padding: 9px 14px;
}

#chevrons .icon
{
    display: block;
    height: 50%;
    color: #d1dede;
    width: 16px;
    height: 16px;
    text-align: right;
}

#options-view-button:checked + #select-button #chevrons i
{
    color: hsl(207, 26%, 17%);
}

.options
{
    position: absolute;
    left: 0;
    width: 150px;
}

#options
{
    position: absolute;
    top: 42px;
    right: 0;
    left: 0;
    width: 198px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 4px;
}

#options-view-button:checked ~ #options
{
    border: 1px solid #e2eded;
    border-color: #eaf1f1 #e4eded #dbe7e7 #e4eded;
}
#options-view-button:checked ~ #options.element-bg
{
    border: 1px solid hsl(207, 26%, 17%);
    border-color: hsl(207, 26%, 17%) hsl(207, 26%, 17%) hsl(207, 26%, 17%) hsl(207, 26%, 17%);
}

.option
{
    position: relative;
    line-height: 1;
    transition: 0.3s ease all;
    z-index: 2;
}

.option i
{
    position: absolute;
    left: 14px;
    padding: 0;
    display: none;
}

#options-view-button:checked ~ #options .option i
{
    display: block;
    padding: 12px 0;
}

.label
{
    display: none;
    padding: 0;
    margin-left: 27px;
}

#options-view-button:checked ~ #options .label
{
    display: block;
    padding: 12px 14px;    
}

.s-c
{
    position: absolute;
    left: 0;
    width: 100%;
    height: 50%;
}

.s-c.top
{
    top: 0;
}

.s-c.bottom
{
    bottom: 0;
}

input[type="radio"]
{
    position: absolute;
    right: 0;
    left: 0;
    width: 100%;
    height: 50%;
    margin: 0;
    opacity: 0;
    cursor: pointer;
}

.s-c:hover ~ i
{
    color: #fff;
    opacity: 0;
}

.s-c:hover
{
    height: 100%;
    z-index: 1;
}



.label
{
    transition: 0.3s ease all;
}

.opt-val
{
    position: absolute;
    left: 14px;
    width: 150px;
    height: 21px;
    opacity: 0;
    background-color: #fff;
    transform: scale(0);
}
.opt-val.element-bg
{
    position: absolute;
    left: 14px;
    width: 150px;
    height: 21px;
    opacity: 0;
    background-color:  hsl(209, 23%, 22%);
    color: white;
    transform: scale(0);
}

.option input[type="radio"]:checked ~ .opt-val
{
    opacity: 1;
    transform: scale(1);
}

.option input[type="radio"]:checked ~ i
{
    top: 0;
    bottom: auto;
    opacity: 1;
    animation: unset;
}

.option input[type="radio"]:checked ~ i, .option input[type="radio"]:checked ~ .label
{
    color:#fff;
}

.option input[type="radio"]:checked ~ .label:before
{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}

#options-view-button:not(:checked) ~ #options .option input[type="radio"]:checked ~ .opt-val
{
    top: -30px;
}

.option:nth-child(1) input[type="radio"]:checked ~ .label:before
{
    border-radius: 4px 4px 0 0;
}

.option:nth-child(1) input[type="radio"]:checked ~ .opt-val
{
    top: -31px;
}


.option:nth-child(2) input[type="radio"]:checked ~ .opt-val
{
    top: -71px;
}



.option:nth-child(3) input[type="radio"]:checked ~ .opt-val
{
    top: -111px;
}


.option:nth-child(4) input[type="radio"]:checked ~ .opt-val
{
    top: -151px;
}



.option:nth-child(5) input[type="radio"]:checked ~ .opt-val
{
    top: -191px;
}

.option:nth-child(6) input[type="radio"]:checked ~ .label:before
{

    border-radius: 0 0 4px 4px;
}
.option input[type="radio"]:checked ~ .label:before
{
    background-color:  hsl(207, 26%, 17%);;
  
}


.option:nth-child(6) input[type="radio"]:checked ~ .opt-val
{
    top: -231px;
}



#option-bg
{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 40px;
    transition: 0.3s ease all;
    z-index: 1;
    display: none;
}

#options-view-button:checked ~ #options #option-bg
{
    display: block;
}

.option:hover .label
{
    color: #fff;
}

.option:nth-child(1):hover ~ #option-bg
{
    top: 0;
    border-radius: 4px 4px 0 0;
}
.option:hover ~ #option-bg
{
    background-color: rgba(101, 101, 104, 0.432);
}
.option:hover ~ #option-bg.element-bg
{
    background-color: rgba(101, 101, 104, 0.432);
}

.option:nth-child(2):hover ~ #option-bg
{
    top: 40px;
}

.option:nth-child(3):hover ~ #option-bg
{
    top: 80px;
}

.option:nth-child(4):hover ~ #option-bg
{
    top: 120px;
}

.option:nth-child(5):hover ~ #option-bg
{
    top: 160px;
}

.option:nth-child(6):hover ~ #option-bg
{
    top: 200px;
    border-radius: 0 0 4px 4px;
}