.container{
    max-width: 1150px;
    margin: auto;
    padding: 0 2rem;
    box-sizing: border-box;
}
.light-bg{
    background: hsl(0, 0%, 98%);
    color:hsl(200, 15%, 8%);
}
.dark-bg{
    background: hsl(207, 26%, 17%);
}

.dark-element-bg{
    background: hsl(209, 23%, 22%);
    color:hsl(0, 0%, 100%); 
}




.error-style-light{
    width: 100%;
    height: 3rem;
    background: #686868;
    color: black;
}
.error-style-dark{
    width: 100%;
    height: 3rem;
    background: #686868;
    color: red;
}

